body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.jumbo-text {
  font-size: 60px;
  color: #ffffff;
  text-shadow: 3px 3px 3px #6392d1;
}

.custom-toggler.navbar-toggler {
  border-color: rgb(255,255,255);
}

.card-header, .modal-header {
background: -webkit-linear-gradient(left, #88B7E5, #6392d1);
background: linear-gradient(to right, #88B7E5, #6392d1);
}

.contact-body {
background-color: #DBCAB8;
}

.contact-link {
  color: #220c10;
}

.contact-link:hover {
  color: #ff1654;
}

.contact-title {
  font-weight: bold;
}

#header{
  background-color: #220c10;
}

#name {
  font-size: 30px;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 60px; /* Set the fixed height of the footer here */
  line-height: 60px; /* Vertically center the text there */
  background-color: #220c10;
  color: #ffffff;
}

.nav-link {
  display: inline;
  padding: 0%;
  margin-left: 3px;
}

.social-icons {
  float: right;
}

.social {
  margin-left: 3px;
}




/* make list items responsive along with ul in About.css */
#list1 {
  -webkit-column-width: 150px;
  -o-column-width: 150px;
  -ms-column-width: 150px;
  column-width: 150px;
}
/* make list items responsive along with ul in About.css */
#list2 {
  -webkit-column-width: 200px;
  -o-column-width: 200px;
  -ms-column-width: 200px;
  column-width: 200px;
}

.clear-fix {
  clear: both;
}

hr {
  background-color: #000;
}

.list-div {
  margin: 0px 0px 16px 40px;
}

/* make list items responsive along with code in TechSkills.css and SoftSkills.css*/
ul{margin:0; padding:0;}

#profile-image {
  float: left;
  margin-right: 20px;
  max-height: 235px;
}

@media (max-width: 480px) {
  #profile-image {
    float: none;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (min-width: 1158px) {
  #top-hr {
    display: none;
  }
}

@media (min-width: 1249px) {
  .top-pad {
    margin-top: 16px;
  }
}
.port-card-body {
  background-color: #6392d1;
  color: #ffffff;
  font-size: 20px;
  padding: 10px;
}

.port-row {
  margin-bottom: 20px;
}

.card-port {
  position: relative;
  margin-bottom: 20px;
}

.card-img-top {
  max-height: 235px;
}

.card-port .hover-text {
  position:absolute;
  top: 0px;
  left:0px;
  display: none;
}
  
.card-port:hover .hover-text {
width: 100%;
height: 100%;
background-color: rgba(12, 11, 11, 0.6);
color: white;
display: block;
}

.hover-text a {
  color: #ffffff;
  font-weight: bold;
}

.skill-port {
  margin-bottom: 0px;
  font-weight: bold;
}

#port-main-card {
  margin-bottom: 20px;
  background-color: transparent;
  border: none;
}


html {
  position: relative;
  min-height: 100%;
  margin: 0px;
  padding: 0px;
  background-size: cover;
}

body {
  min-height: 100%;
  margin-bottom: 60px;  /*Margin bottom by footer height*/

  background: url(/profile-react/static/media/co-view-4.7c44167c.jpg) no-repeat center center fixed;
  background-size: cover;
}


.main-div {
  margin: 20px 0px;
}

.space {
  height: 20px;
}

